*{
  box-sizing: border-box;
}


.body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
width:100vw;
overflow: hidden;
  }
  
  .container {
    width: 100%;
    margin: 0 auto;
    padding: 5px 0;
  }

  .logo{
    width:150px;
  }
  
  header {
    background-color: #333;
    color: white;
    padding: 10px;
  }

  header nav{
    display:flex;
  }
  
  header nav ul {
    display:flex;
    list-style: none;
    padding: 0;
  }
  
  header nav ul li {
    display: inline;
    margin-right: 20px;
  }
  
  header nav ul li a {
    color: white;
    text-decoration: none;
  }
  
  #hero {
    background-image: url('images/Hero\ image.jpg');
    background-size: cover;
    color: white;
    padding: 100px 0;
    text-align: center;
  }
  
  #hero h2 {
    font-size: 3em;
  }
  
  #hero .cta-button {
    padding: 10px 20px;
    background-color: #333;
    color: white;
    text-decoration: none;
    border: none;
    cursor: pointer;
  }
  
  #hero .cta-button:hover {
    background-color: #555;
  }
  
  #services {
    padding: 40px 0;
    background-color: #f9f9f9;
  }
  
  .service-card {
    background-color: #fff;
    padding: 20px;
    margin: 10px;
  }
  
  #about {
    padding: 40px 0;
  }
  
  #contact {
    padding: 40px 0;
    background-color: #f9f9f9;
  }
  
  footer {
    background-color: #333;
    color: white;
    padding: 10px 0;
    text-align: center;
  }
  